import React from 'react';
import {Link, NavLink} from "react-router-dom";
import classes from "./Menu.module.css";

const Menu = () => {
    /*
    *   react routera
    *       a
    *       
    *
    *   Link
    *   NavLinkLink
    * */
    return (
        <div>
            <ul>
                <li>
                    {/*<Link to="/"></Link>*/}
                    <NavLink
                        exact
                        // activeClassName={classes.active}
                        activeStyle={{textDecoration:"underline"}}
                        to="/"></NavLink>
                </li>
                <li>
                    {/*<Link to="/about"></Link>*/}
                    <NavLink
                        exact
                        // activeClassName={classes.active}
                        activeStyle={{textDecoration:"underline"}}
                        to="/about"></NavLink>
                </li>
            </ul>
        </div>
    );
};

export default Menu;
